<template>
  <v-container>
    <v-row class="text-center" dense>
      <v-col cols="12">
        <v-card >
          <v-card-title> <v-icon>mdi-cog</v-icon>&nbsp;Configuration</v-card-title>
          <v-divider class="mx-2 mb-2"></v-divider>
          <v-card-text>
            <v-row>
              <v-col>
                <v-data-table
                  :headers="headers"
                  :items="users"
                  :items-per-page="10"
                  class="elevation-1"
                >
                  <template v-slot:[`item.role`]="{ item }">
                    <v-select
                      :items="roles"
                      v-model="item.role"
                      label="Role"
                      @change="updateRole(item.id,item.role)"
                    ></v-select>
                  </template>
                  <template v-slot:[`item.confirmed`]="{ item }">
                    <v-btn icon @click="confirm(item.id)" :disabled="item.confirmed" color="primary">
                      <v-icon>
                        mdi-check-decagram
                      </v-icon>
                    </v-btn>
                  </template>

                  <template v-slot:[`item.ombrometer`]="{ item }">
                    <v-select
                      :items="ombrometers"
                      v-model="item.ombrometer"
                      label="Ombrometer"
                      multiple
                      return-object
                      @change="updateOmbro(item.id,item.ombrometer)"
                    ></v-select>
                  </template>
                </v-data-table>
              </v-col> 
            </v-row>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { db } from '../firebase/db.js'
import moment from 'moment'

  export default {
    name: 'UserSetting',

    data: () => ({
      roles: ['User', 'Administrator', 'Unassigned'],
      headers: [
        {
          text: 'Name',
          align: 'start',
          value: 'name',
        },
        { text: 'Email', value: 'email', align:'start' },
        { text: 'Role', value: 'role', align:'start' },
        { text: 'Ombrometer', value: 'ombrometer', align:'start' },
        { text: 'Confirm', value: 'confirmed', align:'start' },
      ],
      users:[],
      required: [
        v => !!v || 'This Field is required'
      ], 
      latlngRules: [
        v => !!v || 'This Field is required',
        v => /^-?[0-9]*\.?[0-9]*$/.test(v) || 'Only number is allowed'
      ],
      ombrometers:[],
    }),
    async mounted () {
      let loader= this.$loading.show()
      const date=new Date();
      this.offset=date.getTimezoneOffset()/(-60)
      await db.collection('Ombrometer').get().then((docs)=> {
        this.ombrometers=[];
        docs.forEach((doc)=> {
          this.ombrometers.push({value:doc.id, text:doc.data().station})
        })
      })

      await db.collection('user').onSnapshot((docs)=> {
        this.users=[]
        docs.forEach((doc)=> {
          this.users.push({
            id: doc.id,
            ...doc.data()})
        })
      })
      loader.hide()

    },
    methods: {
      updateTime (time) {
        return moment.unix(time).utcOffset(this.offset).format("HH:mm, DD MMM YYYY")
      },
      async confirm(id) {
        let loader= this.$loading.show()
        await db.collection('user').doc(id).update({
          confirmed:true
        }). then(()=> {
          loader.hide()
        }).catch((err)=> {
          loader.hide()
          console.log(err)
        })
      },
      async updateRole(id,role) {
        let loader= this.$loading.show()
        await db.collection('user').doc(id).update({
          role:role
        }). then(()=> {
          loader.hide()
        }).catch((err)=> {
          loader.hide()
          console.log(err)
        })
      },
      async updateOmbro(id,ombro) {
        let loader= this.$loading.show()
        let ombroIDs=[]
        ombro.forEach((id)=> {
          ombroIDs.push(id.value)
        })
        await db.collection('user').doc(id).update({
          ombrometer:ombro,
          ombrometerIDs:ombroIDs
        }). then(()=> {
          loader.hide()
        }).catch((err)=> {
          loader.hide()
          console.log(err)
        })
      }
    }
  }
</script>
