<template>
  <v-container>
    <v-row>
      <v-col>
        <v-card color="primary" dark>
          <v-card-title>
            <v-select
              v-model="station"
              :items="stations"
              label="Ombrometer"
              prepend-icon="mdi-memory"
              required
              @change="getData(station,tab)"
            ></v-select>
          </v-card-title>
             <v-tabs
              v-model="tab"
              background-color="primary"
              color="white"
              show-arrows
              grow
              @change="getData(station,tab)"
            >
              <v-tabs-slider color="#0897d7"></v-tabs-slider>
              <v-tab
                v-for="item in items"
                :key="item"
              >
                {{ item }}
              </v-tab>
            </v-tabs>

            <v-tabs-items v-model="tab">

              <!-- Daily -->
              <v-tab-item>
                <v-card flat class="text-center">
                  <v-row>
                    <v-col>
                      <v-btn class="float-left" color="primary" icon @click="buttonLeftDaily">
                        <v-icon>mdi-arrow-left</v-icon>
                      </v-btn>
                    </v-col>
                    <v-col>
                      <h2> {{monthName}}</h2>
                    </v-col>
                    <v-col>
                      <v-btn class="float-right" color="primary" icon @click="buttonRightDaily">
                        <v-icon>mdi-arrow-right</v-icon>
                      </v-btn>
                    </v-col>
                  </v-row>
                  <BarChart
                    :chart-options="chartOptions"
                    :chart-data="chartDataDaily"
                    :height="300"
                  />
                </v-card>
              </v-tab-item>

              <!-- Monthly -->
              <v-tab-item>
                <v-card flat class="text-center">
                <v-row>
                  <v-col>
                    <v-btn class="float-left" color="primary" icon @click="buttonLeftMonthly">
                      <v-icon>mdi-arrow-left</v-icon>
                    </v-btn>
                  </v-col>
                  <v-col>
                    <h2> {{year}}</h2>
                  </v-col>
                  <v-col>
                    <v-btn class="float-right" color="primary" icon @click="buttonRightMonthly">
                      <v-icon>mdi-arrow-right</v-icon>
                    </v-btn>
                  </v-col>
                </v-row>
                <BarChart
                  :chart-options="chartOptions"
                  :chart-data="chartDataMonthly"
                  :height="300"
                />
                </v-card>
              </v-tab-item>

              <!-- Yearly -->
              <v-tab-item>
                <v-card flat class="text-center">
                <BarChart
                  :chart-options="chartOptions"
                  :chart-data="chartDataYearly"
                  :height="300"
                />
                </v-card>
              </v-tab-item>
            </v-tabs-items>

            
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { db }from "@/firebase/db.js"
// import moment from 'moment'
import BarChart from '@/components/WaterLevelChart.vue'

const months = ["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"];
// const days = [31,28,31,30,31,30,31,31,30,31,30,31]

export default ({
  components: { BarChart },
  data: () => ({
    time:[],
    devices:[],
    data:[],
    offset:0,

    tab: null,
    items: [
      'Daily', 'Monthly', 'Yearly'
    ],
  
    dialog:false,
    ID:null,
    chartDataDaily: {
      labels: [],
      datasets: [ { label: 'Curah',data: [],backgroundColor: '#3f51b5', } ],
    },
    chartDataMonthly: {
      labels: [],
      datasets: [ { label: 'Curah',data: [],backgroundColor: '#3f51b5', } ],
    },
    chartDataYearly: {
      labels: [],
      datasets: [ { label: 'Curah',data: [],backgroundColor: '#3f51b5', } ],
    },
    chartOptions: {
      responsive: true
    },
    headersDetail: [
        {
          text: 'Tanggal',
          align: 'start',
          value: 'tanggal',
        },
        { text: 'Curah (mm)', value: 'curah' },
      ],
    labels:[],
    datasets:[],
    location:null,
    locations:['WS CIMANUK - CISANGGARUNG', 'WS CITANDUY', 'WS CIWULAN - CILAKI','WS CISADEA - CIBARENO','WS CITARUM', 'WS CILIWUNG - CISADANE'],
    station: null,
    stations:[],
    monthNum:0,
    monthName:'',
    year:2022,
    enableLocation:true,
  }),
  async mounted() {
    let loader = this.$loading.show()
    const date=new Date();
    this.monthNum = date.getMonth()
    this.monthName = months[this.monthNum]
    this.year=date.getFullYear()
    await db.collection('user').doc(localStorage.getItem('uid')).get().then((doc)=> {
      this.stations=[]
      doc.data().ombrometer.forEach((ombro)=> {
        this.stations.push({
          value:ombro.value,
          text:ombro.text
        })
      })
    })
    // let setByLocation=false
    // await db.collection('roleSetting').doc(localStorage.getItem("role")).get().then((doc)=> {
    //   setByLocation=doc.data().location
    // }).catch(() => {
    //   loader.hide()
    // })

    // if (setByLocation) {
    //   this.location=localStorage.getItem('location')
    //   this.enableLocation=false
    //   await db.collection('device').where('location','==',localStorage.getItem('location')).get().then((querySnapshot) => {
    //     this.stations=[]
    //     this.station=null
    //     querySnapshot.forEach((doc) => {
    //       this.stations.push({value:doc.data().macAddress,text:doc.data().station})
    //     });
    //   })
    // }
    loader.hide()
  },
  methods: {
    // async getStations (location) {
    //   await db.collection('device').where('location','==',location).get().then((querySnapshot) => {
    //     this.stations=[]
    //     this.station=null
    //     querySnapshot.forEach((doc) => {
    //       this.stations.push({value:doc.data().macAddress,text:doc.data().station})
    //     });
    //   })
    // },
    async getData(station,tab) {
      if (station!=null) {
        if (tab==0) {
          const date=new Date();
          this.offset=date.getTimezoneOffset()/(-60)
          this.monthName = months[this.monthNum]
          await db.collection('Ombrometer').doc(station).collection('report').orderBy("day","desc").where("month", "==", this.monthNum+1).get().then((collectionSnapshot) => {
            this.datasets =[]
            this.labels=[]
            collectionSnapshot.forEach((doc) => {
              this.labels.push(`${doc.data().day}-${doc.data().month}-${doc.data().year}`)
              this.datasets.push(doc.data().RainRate)
            });
            console.log(this.datasets)
            this.chartDataDaily =  {
              labels: this.labels,
              datasets: [ { label: 'Curah (mm)',data: this.datasets,backgroundColor: '#3f51b5', } ],
            }
          })
        } else if (tab==1) {
          await db.collection('Ombrometer').doc(station).collection('reportMonthly').where('year','==',this.year).orderBy('month','desc').get().then((collectionSnapshot) => {
            this.datasets =[]
            this.labels=[]
            collectionSnapshot.forEach((doc) => {    
              this.labels.push(months[parseInt(doc.data().month)-1])
              this.datasets.push(doc.data().RainRate)
            });
            console.log(this.datasets)
            this.chartDataMonthly =  {
              labels: this.labels,
              datasets: [ { label: 'Curah (mm)',data: this.datasets,backgroundColor: '#3f51b5', } ],
            }
          })
        } else if (tab==2) {
          await db.collection('Ombrometer').doc(station).collection('reportYearly').orderBy('year','desc').get().then((collectionSnapshot) => {
            this.datasets =[]
            this.labels=[]
            collectionSnapshot.forEach((doc) => {    
              this.labels.push(doc.data().year)
              this.datasets.push(doc.data().RainRate)
            });
            console.log(this.datasets)
            this.chartDataYearly =  {
              labels: this.labels,
              datasets: [ { label: 'Curah (mm)',data: this.datasets,backgroundColor: '#3f51b5', } ],
            }
          })
        }
        
      }
      
    },
    buttonLeftDaily() {
      if (this.monthNum!=0) {
        this.monthNum--
      } else {
        this.monthNum=11
        this.year--
      }
      this.getData(this.station,this.tab)
    },
    buttonRightDaily() {
      if (this.monthNum!=11) {
        this.monthNum++
      } else {
        this.monthNum=0
        this.year++
      }
      this.getData(this.station,this.tab)
    },
    buttonLeftMonthly() {
      this.year--
      this.getData(this.station,this.tab)
    },
    buttonRightMonthly() {
      this.year++
      this.getData(this.station,this.tab)
    }
  }
})
</script>

