                      label="E-mail"
                      type="email"
                      prepend-icon="mdi-email"
                      required
                    ></v-text-field>
                    <v-text-field
                      v-model="password"
                      :rules="passwordRules"
                      label="Password"
                      :type="show1 ? 'text' : 'password'"
                      prepend-icon="mdi-lock"
                      :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                      @click:append="show1 = !show1"
                      required
                    ></v-text-field>
                    
                    <h4 style="color:#ff5252">{{errorServer}}</h4>
                    <br>
                    <router-link :to="{name: 'Login'}">
                    <v-btn
                        text
                        x-small
                      >
                      Already Have An Account?
                    </v-btn>
                    </router-link>

                    <v-btn
                      color="warning"
                      class="float-end"
                      @click="Register(email,password)"
                    >
                      Register
                    </v-btn>
                  </v-form> 
                  </v-col>
                </v-row>
              </v-card-text>
