<template>
  <v-container>
    <v-row class="text-center" dense>
      <v-col cols="6">
        <v-card >
          <v-card-title> <v-icon>mdi-weather-pouring</v-icon>&nbsp;Rain Rate</v-card-title>
          <v-divider class="mx-2 mb-2"></v-divider>
          <v-card-text><h1>{{RainRate}} mm</h1>
          <br>
          Last update: {{updateTime(timeStamp)}}
          </v-card-text>
        </v-card>
      </v-col>
      <v-col>
        <v-card >
          <v-card-title> Daily Rain Rate</v-card-title>
          <v-divider class="mx-2 mb-2"></v-divider>
          <v-card-text><h1>{{RainRateDay}} mm</h1>
          <br>
          Last rain: {{updateTime(lastRain)}}
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>

    <v-row class="text-left" dense>
      <v-col cols="12">
        <v-card >
          <v-card-title> <v-icon>mdi-speedometer</v-icon>&nbsp;Device Details</v-card-title>
          <v-divider class="mx-2 mb-2"></v-divider>
          <v-card-text>
            Signal: {{signal}} <br>
            Battery Voltage: {{battery}} Volt <br>
            Last update: {{updateTime(timeStamp)}} <br>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>

    <v-row class="text-left" dense>
      <v-col cols="12">
        <v-card >
          <v-card-title> <v-icon>mdi-switch</v-icon>&nbsp;Water Level</v-card-title>
          <v-divider class="mx-2 mb-2"></v-divider>
          <v-card-subtitle><h2 class="text-center">{{waterLevelStatus}}</h2></v-card-subtitle>
          <v-card-text>
            <div id="canvasWrapper" style="position: relative; height: 350px">
              <BarChart :chartData="dataWaterLevel" :chartOptions="optionsWaterLevel" :key="updateWaterLevel"/>
            </div>
            <br>
            <span v-for="(item,index) in Switch" :key="index">
              Level <b>{{index+1}}</b> 
              <span v-if="index<2">(Normal) :&nbsp;</span>
              <span v-else-if="index<4">(Waspada) :&nbsp;</span>
              <span v-else-if="index<6">(Siaga) :&nbsp;</span>
              <span v-else>(Awas) :&nbsp;</span>
              <b>{{item=="48"||item=="0"?'Nyala':'Mati'}}</b> <br>
            </span>
            Last Update: {{updateTime(TimeIO)}}
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>

    <v-row class="text-left" dense v-if="isLoRa">
      <v-col cols="12">
        <v-card >
          <v-card-title> <v-icon>mdi-switch</v-icon>&nbsp;Water Level Slave</v-card-title>
          <v-divider class="mx-2 mb-2"></v-divider>
          <v-card-subtitle><h3 class="text-center">{{updateTime(TimeIOLoRa)}}</h3></v-card-subtitle>
          <v-card-text>
            <div id="canvasWrapper" style="position: relative; height: 350px">
              <BarChart :chartData="dataWaterLevelSlave" :chartOptions="optionsWaterLevelSlave" :key="updateWaterLevelSlave"/>
            </div>
            <br>
            <!-- <span v-for="(item,index) in Switch" :key="index">
              Level <b>{{index+1}}</b> 
              <span v-if="index<2">(Normal) :&nbsp;</span>
              <span v-else-if="index<4">(Waspada) :&nbsp;</span>
              <span v-else-if="index<6">(Siaga) :&nbsp;</span>
              <span v-else>(Awas) :&nbsp;</span>
              <b>{{item=="48"||item=="0"?'Nyala':'Mati'}}</b> <br>
            </span> -->
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>

    <v-row class="text-left" dense>
      <v-col cols="12">
        <v-card >
          <v-card-title> <v-icon>mdi-sd</v-icon>&nbsp;Download Data</v-card-title>
          <v-divider class="mx-2 mb-2"></v-divider>
          <v-card-text>
            <v-data-table
              :headers="headers"
              :items="Files"
              :items-per-page="10"
              class="elevation-1"
              mobile-breakpoint="300"
            >
              <template v-slot:[`item.timestamp`]="{ item }">
                {{updateTime(item.timestamp)}}
              </template>
              <template v-slot:[`item.id`]="{ item }">
                <v-btn icon @click="clicked(item)" color="primary">
                  <v-icon>
                    mdi-download
                  </v-icon>
                </v-btn>
              </template>
            </v-data-table>

            <v-dialog
              v-model="dialog"
              max-width="600"
            >
              <v-card>
                <v-toolbar
                  color="primary text-h6"
                  dark
                >{{ deviceName }}
                <v-spacer></v-spacer>
                
                </v-toolbar>
                <v-card-actions>
                  <download-csv
                    class   = "btn btn-default"
                    :data   = "downloadData"
                    :name    = "csvTitle"
                    separator-excel>
                    <v-btn class="float-start mb-2" color="primary" text><v-icon left>mdi-download</v-icon>Download</v-btn>
                  </download-csv>
                  <v-spacer></v-spacer>
                  <v-btn
                    color="primary"
                    text
                    @click="dialog = false"
                  >
                    Close
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>
          </v-card-text>
        </v-card>

        <v-row class="text-center">
          <v-col>
            <v-btn class="error darken-1 mt-2" @click="reset($props.device_id)"> <v-icon left>mdi-restart</v-icon>Restart Device</v-btn>
          </v-col>
        </v-row>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { db } from '../firebase/db.js'
import csvtojson from 'csvtojson'
import moment from 'moment'
import BarChart from './WaterLevelChart.vue'

  export default {
    props: ['device_id'],
    name: 'DashboardData',

    components: {
      BarChart
    },

    data: () => ({
      RainRate:null,
      RainRateDay:null,
      lastRain:null,
      timeStamp:null,
      offset:null,
      signal:null,
      battery:null,
      TimeIO:null,
      TimeIOLoRa:null,
      IOLoRa:[],
      Switch:[],
      headers: [
        {
          text: 'Date',
          align: 'start',
          sortable: false,
          value: 'timestamp',
        },
        { text: 'Download', value: 'id', align:'end' },
      ],
      Files:[],
      csvTitle:null,
      deviceName:null,
      downloadData:[],
      dialog:false,
      updateWaterLevel:false,
      optionsWaterLevel: {
        responseive: true,
        maintainAspectRatio: false,
        layout: {
          padding:0
        },
        plugins: {
          legend:{
            display:false
          }
        },
        scales: {
            y: {
                title: {
                  display: false,
                  text: 'Level'
                },
                min: 0,
                max: 7,
                ticks: {
                  callback: function(index) {
                    if (index<=2) {
                      return 'Normal'
                    } else if (index<=4) {
                      return 'Waspada'
                    } else if (index<=6) {
                      return 'Siaga'
                    } else {
                      return 'Awas'
                    }
                  }
                }
            },
            x: {
              title: {
                display: false,
              }
            }
        }
      },
      dataWaterLevel:{
        datasets: [
          {
            label: 'Water Level',
            fill: false,
            borderColor: '#3949AB',
            backgroundColor: '#3949AB',
            showLine: true,
            data: [
            ]
          },
        ]
      },
      waterLevelStatus:null,
      updateWaterLevelSlave:false,
      optionsWaterLevelSlave: {
        responseive: true,
        maintainAspectRatio: false,
        layout: {
          padding:0
        },
        plugins: {
          legend:{
            display:false
          }
        },
        scales: {
            y: {
                title: {
                  display: false,
                  text: 'Level'
                },
                min: 0,
                max: 7,
                ticks: {
                  callback: function(index) {
                    if (index<=2) {
                      return 'Normal'
                    } else if (index<=4) {
                      return 'Waspada'
                    } else if (index<=6) {
                      return 'Siaga'
                    } else {
                      return 'Awas'
                    }
                  }
                }
            },
            x: {
              title: {
                display: false,
              }
            }
        }
      },
      dataWaterLevelSlave:{
        datasets: [
          {
            label: 'Water Level',
            fill: false,
            borderColor: '#3949AB',
            backgroundColor: '#3949AB',
            showLine: true,
            data: [
            ]
          },
        ]
      },
      isLoRa:false,
    }),
    async mounted () {
      const date=new Date();
      this.offset=date.getTimezoneOffset()/(-60)
      
      await db.collection('Ombrometer').doc(this.$props.device_id).onSnapshot((doc)=> {
        this.RainRate=parseFloat(doc.data().RainRate).toFixed(2)
        this.RainRateDay=parseFloat(doc.data().RainRateDay).toFixed(2)
        this.lastRain=parseInt(doc.data().TimeRain)
        this.timeStamp=parseInt(doc.data().Timestamp)
        this.signal=parseInt(doc.data().Signal)
        this.battery=parseFloat(doc.data().Battery).toFixed(3)
        this.Switch=doc.data().IO
        this.TimeIO=doc.data().TimeIO
        this.TimeIOLoRa=doc.data().TimeIOLoRa
        this.IOLoRa = doc.data().IOLoRa
        this.deviceName=doc.data().station
        this.dataWaterLevel.datasets[0].data[0]={
          x:this.updateTime(this.timeStamp),
          y:0,
        }
        this.waterLevelStatus='Normal'
        if (this.Switch!=undefined) {
          for (let i=0;i<7;i++) {
            if (this.Switch[i]=="48"|| this.Switch[i]=="0") {
              this.dataWaterLevel.datasets[0].data[0]={
                x:this.updateTime(this.timeStamp),
                y:i+1,
              }
              
              if (i<2) {
                this.waterLevelStatus='Normal'
              } else if (i<4) {
                this.waterLevelStatus='Waspada'
              } else if (i<6) {
                this.waterLevelStatus='Siaga'
              } else {
                this.waterLevelStatus='Awas'
              }
            }
          }
        }
        
        this.updateWaterLevel=!this.updateWaterLevel


        if (this.IOLoRa!=undefined) {
          for (let i=0;i<7;i++) {
            
            this.dataWaterLevelSlave.datasets[0].data[i]={
              x:(i+1).toString(),
              y:this.IOLoRa[i].length>1?this.IOLoRa[i].lastIndexOf('0')+1:0,
            }
          }
        }
        
        this.updateWaterLevelSlave=!this.updateWaterLevelSlave
        this.isLoRa=doc.data().isLoRa
      })
      await db.collection('Ombrometer').doc(this.$props.device_id).collection('SDFiles').orderBy('time','desc').onSnapshot((docs)=> {
        this.Files=[]
        docs.forEach((doc)=> {
          this.Files.push({
            timestamp: doc.data().time/1000,
            data: doc.data().data,
            id:doc.id
          })
        })
      })
    },
    methods: {
      updateTime (time) {
        return moment.unix(time).utcOffset(this.offset).format("HH:mm, DD MMM YYYY")
      },
      clicked(item) {
        this.dialog=true
        this.csvTitle=`LOG_${this.deviceName}-${this.updateTime(item.timestamp)}.csv`
        this.downloadData=[]
        csvtojson()
          .fromString(item.data)
          .then((csvRow)=>{ 
              this.downloadData=csvRow
          })
        console.log(this.downloadData)
      },
      async reset(id) {
        await db.collection('Ombrometer').doc(id).update({
          resetDevice:1
        })
      }
    }
  }
</script>
