<template>
  <v-container>
    <v-row class="mt-n4 mb-n8">
      <v-col>
        <span class="text-h6" style="color:#1F245B">Ombrometer</span>
      </v-col>
    </v-row>
    <v-row class="my-n8">
      <v-col>
        <v-checkbox
          v-model="red"
          label="Alert"
          color="red darken-3"
          hide-details
        ></v-checkbox>
      </v-col>
      <v-col>
        <v-checkbox
          v-model="blue"
          label="Hujan"
          color="primary"
          hide-details
        ></v-checkbox>
      </v-col>
      <v-col>
        <v-checkbox
          v-model="green"
          label="Tidak Hujan"
          color="green darken-1"
          hide-details
        ></v-checkbox>
      </v-col>
      <v-col>
        <v-checkbox
          v-model="grey"
          label="Mati"
          color="grey"
          hide-details
        ></v-checkbox>
      </v-col>
    </v-row>
    <v-row class="mt-8 mb-n8">
      <v-col>
        <span class="text-h6" style="color:#1F245B">Water Level</span>
      </v-col>
    </v-row>
    <v-row class="my-n8">
      <v-col>
        <v-checkbox
          v-model="Awas"
          label="Awas"
          color="red darken-3"
          hide-details
        ></v-checkbox>
      </v-col>
      <v-col>
        <v-checkbox
          v-model="Siaga"
          label="Siaga"
          color="orange darken-3"
          hide-details
        ></v-checkbox>
      </v-col>
      <v-col>
        <v-checkbox
          v-model="Waspada"
          label="Waspada"
          color="yellow darken-3"
          hide-details
        ></v-checkbox>
      </v-col>
    </v-row>

    
    
    <v-row>
      <v-col>
        <v-card>
        <l-map
          v-if="showMap"
          :zoom="zoom"
          :center="center"
          :options="mapOptions"
          style="height: 70vH"
          @update:center="centerUpdate"
          @update:zoom="zoomUpdate"
        >
          <l-tile-layer
            :url="url"
          />

          <!-- Ombrometer -->
          <span v-for="item in devices" :key="item.station">
          <span v-if="item.condition === 'Hujan' && blue">
            <l-marker 
              :lat-lng="item.map"
              :icon="item.icon"
              >
              <l-popup>
                <div>
                  {{item.station}}
                  <p>
                    Condition: {{item.condition}}<br>
                    Status Ombrometer: {{item.statusOmbro}}<br>
                    Curah: {{item.curah}} mm<br>
                    Battery: {{item.battery}} Volt <br>
                    Water Level: Level {{item.level}} <br>
                    Signal: {{item.signal}}
                  </p>
                </div>
              </l-popup>
            </l-marker>
          </span>
          <span v-if="item.condition === 'Tidak Hujan' && green">
            <l-marker 
              :lat-lng="item.map"
              :icon="item.icon"
              >
              <l-popup>
                <div>
                  {{item.station}}
                  <p>
                    Condition: {{item.condition}}<br>
                    Status Ombrometer: {{item.statusOmbro}}<br>
                    Curah: {{item.curah}} mm<br>
                    Battery: {{item.battery}} Volt<br>
                    Water Level: Level {{item.level}} <br>
                    Signal: {{item.signal}}
                  </p>
                </div>
              </l-popup>
            </l-marker>
          </span>
          <span v-if="red && item.condition=='Alert'">
            <l-marker 
              :lat-lng="item.map"
              :icon="item.icon"
              >
              <l-popup>
                <div>
                  {{item.station}}
                  <p>
                    Condition: {{item.condition}}<br>                    
                    Status Ombrometer: {{item.statusOmbro}}<br>
                    Curah: {{item.curah}} mm<br>
                    Battery: {{item.battery}} Volt<br>
                    Water Level: Level {{item.level}} <br>
                    Signal: {{item.signal}}
                  </p>
                </div>
              </l-popup>
            </l-marker>
          </span>
          <span v-if="grey && item.condition==='Mati'">
            <l-marker 
              :lat-lng="item.map"
              :icon="item.icon"
              >
              <l-popup>
                <div>
                  {{item.station}}
                  <p>
                    Condition: {{item.condition}}<br>                    
                    Status Ombrometer: {{item.statusOmbro}}<br>
                    Curah: {{item.curah}} mm<br>
                    Battery: {{item.battery}} Volt<br>
                    Water Level: Level {{item.level}} <br>
                    Signal: {{item.signal}}
                  </p>
                </div>
              </l-popup>
            </l-marker>
          </span>
          <span v-if="Awas && item.condition=='Awas'">
            <l-marker 
              :lat-lng="item.map"
              :icon="item.icon"
              >
              <l-popup>
                <div>
                  {{item.station}}
                  <p>
                    Condition: {{item.condition}}<br>                    
                    Status Ombrometer: {{item.statusOmbro}}<br>
                    Curah: {{item.curah}} mm<br>
                    Battery: {{item.battery}} Volt<br>
                    Water Level: Level {{item.level}} <br>
                    Signal: {{item.signal}}
                  </p>
                </div>
              </l-popup>
            </l-marker>
          </span>
          <span v-if="Siaga && item.condition=='Siaga'">
            <l-marker 
              :lat-lng="item.map"
              :icon="item.icon"
              >
              <l-popup>
                <div>
                  {{item.station}}
                  <p>
                    Condition: {{item.condition}}<br>                    
                    Status Ombrometer: {{item.statusOmbro}}<br>
                    Curah: {{item.curah}} mm<br>
                    Battery: {{item.battery}} Volt<br>
                    Water Level: Level {{item.level}} <br>
                    Signal: {{item.signal}}
                  </p>
                </div>
              </l-popup>
            </l-marker>
          </span>
          <span v-if="Waspada && item.condition=='Waspada'">
            <l-marker 
              :lat-lng="item.map"
              :icon="item.icon"
              >
              <l-popup>
                <div>
                  {{item.station}}
                  <p>
                    Condition: {{item.condition}}<br>                    
                    Status Ombrometer: {{item.statusOmbro}}<br>
                    Curah: {{item.curah}} mm<br>
                    Battery: {{item.battery}} Volt<br>
                    Water Level: Level {{item.level}} <br>
                    Signal: {{item.signal}}
                  </p>
                </div>
              </l-popup>
            </l-marker>
          </span>
          </span>
        </l-map>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { db }from "@/firebase/db.js"
// import moment from 'moment'
import { latLng, icon } from "leaflet";
import { LMap, LTileLayer, LMarker, LPopup,  } from "vue2-leaflet";

import { Icon } from 'leaflet';

delete Icon.Default.prototype._getIconUrl;
Icon.Default.mergeOptions({
  iconRetinaUrl: require('leaflet/dist/images/marker-icon-2x.png'),
  iconUrl: require('leaflet/dist/images/marker-icon.png'),
  shadowUrl: require('leaflet/dist/images/marker-shadow.png'),
});


export default {
  name: "Map",
  components: {
    LMap,
    LTileLayer,
    LMarker,
    LPopup,
  },
  data() {
    return {
      red:true,
      green:true,
      blue:true,
      grey:true,
      Awas:true,
      Siaga:true,
      Waspada:true,
      role: null,
      zoom: 8,
      center: latLng(-7.5, 110.25),
      url: 'https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png',
      devices:[],
      UIDs:[],
      mapOptions: {
        zoomSnap: 0.5
      },
      showMap: true,
      closeButton: false,
      Bicon: icon({
        iconRetinaUrl: require('@/assets/Map/BSphere.png'),
        iconUrl: require('@/assets/Map/BSphere.png'),
        iconAnchor: [9,9],
        popupAnchor: [0,0],
        // shadowUrl: require('leaflet/dist/images/marker-shadow.png'),
        // shadowAnchor: [14,38],
      }),
      Ricon: icon({
        iconRetinaUrl: require('@/assets/Map/RSphere.png'),
        iconUrl: require('@/assets/Map/RSphere.png'),
        iconAnchor: [9,9],
        popupAnchor: [0,0],
        // shadowUrl: require('leaflet/dist/images/marker-shadow.png'),
        // shadowAnchor: [14,38],
      }),
      Gicon: icon({
        iconRetinaUrl: require('@/assets/Map/GSphere.png'),
        iconUrl: require('@/assets/Map/GSphere.png'),
        iconAnchor: [9,9],
        popupAnchor: [0,0],
        // shadowUrl: require('leaflet/dist/images/marker-shadow.png'),
        // shadowAnchor: [14,38],
      }),
      Yicon: icon({
        iconRetinaUrl: require('@/assets/Map/YSphere.png'),
        iconUrl: require('@/assets/Map/YSphere.png'),
        iconAnchor: [9,9],
        popupAnchor: [0,0],
        // shadowUrl: require('leaflet/dist/images/marker-shadow.png'),
        // shadowAnchor: [14,38],
      }),
      redTMA: icon({
        iconRetinaUrl: require('@/assets/Map/resized/redTMA.png'),
        iconUrl: require('@/assets/Map/resized/redTMA.png'),
        iconAnchor: [9,9],
        popupAnchor: [0,0],
        // shadowUrl: require('leaflet/dist/images/marker-shadow.png'),
        // shadowAnchor: [14,38],
      }),
      yellowTMA: icon({
        iconRetinaUrl: require('@/assets/Map/resized/yellowTMA.png'),
        iconUrl: require('@/assets/Map/resized/yellowTMA.png'),
        iconAnchor: [9,9],
        popupAnchor: [0,0],
        // shadowUrl: require('leaflet/dist/images/marker-shadow.png'),
        // shadowAnchor: [14,38],
      }),
      orangeTMA: icon({
        iconRetinaUrl: require('@/assets/Map/resized/orangeTMA.png'),
        iconUrl: require('@/assets/Map/resized/orangeTMA.png'),
        iconAnchor: [9,9],
        popupAnchor: [0,0],
        // shadowUrl: require('leaflet/dist/images/marker-shadow.png'),
        // shadowAnchor: [14,38],
      }),
    };
  },
  async mounted () {
    let loader = this.$loading.show()
    this.UIDs=[]
    await db.collection('user').doc(localStorage.getItem('uid')).get().then((doc)=> {
      doc.data().ombrometer.forEach((IDs)=> {
        this.UIDs.push(IDs.value)
      })
    })
    db.collection('Ombrometer').onSnapshot((docs)=> {
      this.devices=[]
      docs.forEach((doc)=> {
        if (this.UIDs.findIndex((e)=> e==doc.id)!=-1) {
          let waterLevel = doc.data().IO[6]=="48"?'7':doc.data().IO[5]=="48"?'6':doc.data().IO[4]=="48"?'5':doc.data().IO[3]=="48"?'4':doc.data().IO[2]=="48"?'3':doc.data().IO[1]=="48"?'2':doc.data().IO[0]=="48"?'1':'0'
          this.devices.push({
            id:doc.id,
            station: doc.data().station,
            battery: doc.data().Battery,
            signal: doc.data().Signal,
            condition: (Date.now()/1000)-parseFloat(doc.data().Timestamp)>2*parseFloat(doc.data().period)*10? 'Mati' : waterLevel>'2'? waterLevel>'4'? waterLevel>'6'? 'Awas': 'Siaga' : 'Waspada': doc.data().RainStart=="1"? doc.data().RainStop=="1"? 'Tidak Hujan': doc.data().Alert!="0"? 'Alert': 'Hujan' : 'Tidak Hujan',
            icon: (Date.now()/1000) - parseFloat(doc.data().Timestamp)>2*parseFloat(doc.data().period)*10? this.Yicon : waterLevel>'2'? waterLevel>'4'? waterLevel>'6'? this.redTMA: this.orangeTMA : this.yellowTMA : doc.data().RainStart=="1"? doc.data().RainStop=="1"? this.Gicon: doc.data().Alert!="0"? this.Ricon: this.Bicon : this.Gicon,
            map:latLng(parseFloat(doc.data().latitude),parseFloat(doc.data().longitude)),
            curah: parseFloat(doc.data().RainRate).toFixed(2),
            statusOmbro: (Date.now()/1000)-parseFloat(doc.data().Timestamp)>2*parseFloat(doc.data().period)*10? 'Mati' : doc.data().RainStart=="1"? doc.data().RainStop=="1"? 'Tidak Hujan': doc.data().Alert!="0"? 'Alert': 'Hujan' : 'Tidak Hujan',
            level: doc.data().IO[6]=="48"?'7':doc.data().IO[5]=="48"?'6':doc.data().IO[4]=="48"?'5':doc.data().IO[3]=="48"?'4':doc.data().IO[2]=="48"?'3':doc.data().IO[1]=="48"?'2':doc.data().IO[0]=="48"?'1':'0'
          })
        }
      })
    })
    loader.hide()
  },
  methods: {
    zoomUpdate(zoom) {
      this.currentZoom = zoom;
    },
    centerUpdate(center) {
      this.currentCenter = center;
    },
    showLongText() {
      this.showParagraph = !this.showParagraph;
    },
    closeButtons () {
      console.log("HELLO")
    },
  }
};
</script>

<style lang="scss">
  .vue2leaflet-map {
    z-index: 1;
  }
</style>
