<template>
  <v-container>
    <v-row class="text-center" dense>
      <v-col cols="12">
        <v-card >
          <v-card-title> <v-icon>mdi-cog</v-icon>&nbsp;Configuration</v-card-title>
          <v-divider class="mx-2 mb-2"></v-divider>
          <v-card-text>
              <v-row>
                <v-col>
                <v-form
                  ref="form"
                  v-model="valid"
                  lazy-validation
                >
                  <v-text-field
                    v-model="station"
                    :rules="required"
                    label="Nama Device"
                    type="text"
                    prepend-icon="mdi-rename"
                    required
                  ></v-text-field>
                  <v-text-field
                    v-model="period"
                    :rules="latlngRules"
                    label="Periode Report (s)"
                    type="text"
                    prepend-icon="mdi-clock-time-ten-outline"
                    required
                  ></v-text-field>
                  <v-text-field
                    v-model="periodSD"
                    :rules="latlngRules"
                    label="Periode Simpan Data SD Card (s)"
                    type="text"
                    prepend-icon="mdi-clock-time-two-outline"
                    required
                  ></v-text-field>
                  <v-text-field
                    v-model="tipmm"
                    :rules="latlngRules"
                    label="Milimeter Hujan per Tip"
                    type="text"
                    prepend-icon="mdi-weather-pouring"
                    required
                  ></v-text-field>
                  <v-text-field
                    v-model="alert"
                    :rules="latlngRules"
                    label="Nilai Batas Notifikasi Hujan"
                    type="text"
                    prepend-icon="mdi-alert"
                    required
                  ></v-text-field>

                  <v-text-field
                    v-model="stopTime"
                    :rules="latlngRules"
                    label="Lama Waktu Menunggu Hujan Berhenti (s)"
                    type="text"
                    prepend-icon="mdi-timer"
                    required
                  ></v-text-field>
                  
                  <v-text-field
                    v-model="latlng.lat"
                    :rules="latlngRules"
                    label="Latitude"
                    type="text"
                    prepend-icon="mdi-latitude"
                    required
                  ></v-text-field>
                  <v-text-field
                    v-model="latlng.lng"
                    :rules="latlngRules"
                    label="Longitude"
                    type="text"
                    prepend-icon="mdi-longitude"
                    required
                  ></v-text-field>

                  <v-checkbox
                    v-model="isLoRa"
                    label="Memiliki LoRa Slave"
                    prepend-icon="mdi-transit-connecion-variant"
                  ></v-checkbox>

                  <l-map
                    v-if="showMap"
                    :zoom="zoom"
                    :center="center"
                    :options="mapOptions"
                    style="height: 30vH"
                    @update:center="centerUpdate"
                    @update:zoom="zoomUpdate"
                  >
                    <l-tile-layer
                      :url="url"
                    />
                    <l-marker
                      :draggable="true"
                      :lat-lng.sync="latlng"
                      :icon="Bicon"
                    >
                    </l-marker>

                  </l-map>
                  <br>
                  <v-btn
                    class="float-right success"
                    rounded
                    @click="Save()"
                  >
                    Save
                  </v-btn>
                </v-form>
              </v-col> 
            </v-row>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { db } from '../firebase/db.js'
import moment from 'moment'

import { latLng, icon } from "leaflet";
import { LMap, LTileLayer, LMarker,  } from "vue2-leaflet";

import { Icon } from 'leaflet';

delete Icon.Default.prototype._getIconUrl;
Icon.Default.mergeOptions({
  iconRetinaUrl: require('leaflet/dist/images/marker-icon-2x.png'),
  iconUrl: require('leaflet/dist/images/marker-icon.png'),
  shadowUrl: require('leaflet/dist/images/marker-shadow.png'),
});

  export default {
    props: ['device_id'],
    name: 'ConfigurationData',
    components: {
      LMap,
      LTileLayer,
      LMarker,
    },

    data: () => ({
      alert:null,
      period:null,
      periodSD:null,
      station:null,
      stopTime:null,
      tipmm:null,
      isLoRa:false,
      valid:false,
      required: [
        v => !!v || 'This Field is required'
      ], 
      latlngRules: [
        v => !!v || 'This Field is required',
        v => /^-?[0-9]*\.?[0-9]*$/.test(v) || 'Only number is allowed'
      ],
      
      zoom: 8,
      center: latLng(-7.5, 110.25),
      url: 'https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png',
      mapOptions: {
        zoomSnap: 0.5
      },
      showMap: true,
      closeButton: false,
      Bicon: icon({
        iconRetinaUrl: require('@/assets/Map/BSphere.png'),
        iconUrl: require('@/assets/Map/BSphere.png'),
        iconAnchor: [9,9],
        popupAnchor: [0,0],
        // shadowUrl: require('leaflet/dist/images/marker-shadow.png'),
        // shadowAnchor: [14,38],
      }),
      latlng:null,
    }),
    async mounted () {
      const date=new Date();
      this.offset=date.getTimezoneOffset()/(-60)
      await db.collection('Ombrometer').doc(this.$props.device_id).onSnapshot((doc)=> {
        this.tipmm=doc.data().tipmm
        this.alert=doc.data().alert
        this.stopTime=doc.data().stopTime*10
        this.station=doc.data().station
        this.periodSD=doc.data().periodSD*10
        this.period=doc.data().period*10,
        this.latlng=latLng(parseFloat(doc.data().latitude),parseFloat(doc.data().longitude))
        this.isLoRa= doc.data().isLoRa
      })
    },
    methods: {
      updateTime (time) {
        return moment.unix(time).utcOffset(this.offset).format("HH:mm, DD MMM YYYY")
      },
      async Save() {
        let loader= this.$loading.show()
        await db.collection('user').where('ombrometerIDs', 'array-contains',this.$props.device_id).get().then((docs)=> {
          docs.forEach(async(doc)=> {
            let i= doc.data().ombrometer.findIndex((e)=> e.value==this.$props.device_id)
            let newList=doc.data().ombrometer
            newList[i]={
              value:this.$props.device_id,
              text: this.station
            }
            await db.collection('user').doc(doc.id).update({
              ombrometer: newList
            })
          })
        })
        
        await db.collection('Ombrometer').doc(this.$props.device_id).update({
          tipmm: this.tipmm,
          alert: this.alert,
          stopTime: Math.floor(this.stopTime/10),
          station: this.station,
          periodSD: Math.floor(this.periodSD/10),
          period: Math.floor(this.period/10),
          latitude:this.latlng.lat,
          longitude:this.latlng.lng,
          isLoRa: this.isLoRa
        }). then(()=> {
          loader.hide()
        }).catch((err)=> {
          loader.hide()
          console.log(err)
        })

        
      },
      zoomUpdate(zoom) {
        this.currentZoom = zoom;
      },
      centerUpdate(center) {
        this.currentCenter = center;
      },
      showLongText() {
        this.showParagraph = !this.showParagraph;
      },
      closeButtons () {
        console.log("HELLO")
      },
    }
  }
</script>

<style lang="scss">
  .vue2leaflet-map {
    z-index: 1;
  }
</style>
